.controlsContainer {
  width: 30vw;
}

.colorsContainer,
.texturesContainer {
  display: flex;
  column-gap: 15px;
}

.controlItem {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 0 5px 2px rgb(170, 206, 252);
  transition: 0.5s;
}

.controlItem:hover {
  box-shadow: 0 0 7px 4px rgb(95, 163, 252);
}

.texturesContainer .controlItem {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 80px;
  border-radius: 25%;
}

.bgColorContainer {
  overflow: hidden;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  box-shadow: 0 0 5px 2px rgb(170, 206, 252);
  transition: 0.5s;
}

input[type='color'] {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background: none;
  border: 0;
  cursor: pointer;
  width: 60px;
  height: 60px;
  padding: 0;
}
input[type='color']::-webkit-color-swatch-wrapper {
  padding: 0;
}
input[type='color']::-webkit-color-swatch {
  border: none;
}

@media (max-width: 850px) {
  .controlsContainer {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 80vw;
    align-items: center;
  }
  
  .controlsContainer div {
    display: flex;
    gap: 10px;
    align-items: center;
  }
}
